.search-link {
  background-color: #81b3d2;
  font-family: "pt-sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  margin: 0 auto;
  color: #ffffff;
  &:hover {
    background-color: #669EC0;
  }
  &:active {
    background-color: #669EC0;
    color: rgba(255, 255, 255, 0.3);
  }
  &:disabled {
    background-color: #E5E5E5;
  }

}
